body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #111111;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
    padding: 0 30px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 1220px;
    padding: 0 40px;
  }
}
.button {
  padding: 10px 0;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  width: 100%;
  color: #ffffff;
  text-align: center;
  background-color: #68b738;
  border: none;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button--down {
  opacity: 0.3;
}
.button--grey {
  background-color: #f2f2f2;
  color: #444444;
}
.button--grey:hover,
.button--grey:focus {
  background-color: #ebebeb;
}
.page-header {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.page-header__logo {
  height: auto;
  text-align: left;
}
.page-header__logo-image {
  height: 65px;
  width: 190px;
}
.page-header__logo-image:hover,
.page-header__logo-image:focus,
.page-header__logo-image:active {
  opacity: 0.8;
}
.page-header__logo-image--down {
  opacity: 0.6;
}
.page-header__logo-image--down:hover,
.page-header__logo-image--down:focus,
.page-header__logo-image--down:active {
  opacity: 0.6;
  cursor: auto;
}
.page-header__nav {
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .page-header {
    flex-direction: row;
    justify-content: space-between;
  }
  .page-header__logo {
    width: 190px;
    height: auto;
  }
  .page-header__logo-image {
    display: block;
    width: 175px;
    height: auto;
    margin-top: 10px;
  }
  .page-header__nav {
    width: 515px;
  }
}
@media (min-width: 1300px) {
  .page-header {
    margin-bottom: 45px;
  }
  .page-header__logo {
    height: auto;
    max-width: 245px;
  }
  .page-header__logo-image {
    display: block;
    width: 210px;
    height: auto;
    margin-top: 35px;
  }
  .page-header__nav {
    width: 895px;
  }
}
.main-nav {
  width: 100%;
  margin: 0 auto;
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: -45px;
  left: 221px;
  z-index: 2;
  display: block;
  width: 20px;
  height: 24px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 1px;
  width: 20px;
  height: 2px;
  background-color: #111111;
  box-shadow: 0 8px 0 0 #111111, 0 16px 0 0 #111111;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #111111;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: 445px;
    margin: 0;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__wrapper {
    width: auto;
  }
}
@media (min-width: 1300px) {
  .main-nav {
    width: 485px;
    z-index: 3;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
}
.site-list__item {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  font-weight: 500;
}
.site-list__link {
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  color: #111111;
  padding-top: 25px;
  padding-bottom: 25px;
  border-top: 1px solid #e6e6e6;
}
.site-list__link[href]:hover {
  opacity: 0.6;
}
.site-list__link--down[href] {
  opacity: 0.3;
  cursor: auto;
}
.site-list__link--down[href]:hover,
.site-list__link--down[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-top: 25px;
  }
  .site-list__item {
    margin-right: 30px;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    font-weight: 400;
  }
  .site-list__item:last-child {
    border-bottom: none;
    margin-right: 0;
  }
  .site-list__link {
    padding-top: 0;
    padding-bottom: 9px;
    border-top: none;
  }
  .site-list__link--current {
    border-bottom: 2px solid #68b738;
  }
  .site-list__link--inner-current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1300px) {
  .site-list {
    margin-top: 45px;
  }
  .site-list__link {
    color: #ffffff;
  }
  .site-list__link--current {
    border-bottom: 2px solid #ffffff;
  }
  .site-list__link--inner {
    color: #111111;
  }
}
.promo {
  margin-bottom: 20px;
  position: relative;
}
.promo__wrapper {
  padding-top: 30px;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  position: relative;
}
.promo::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 306px;
  width: 100%;
  background-color: #65a93c;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  z-index: -1;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .promo::before {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
.promo__title {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  color: #ffffff;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}
.promo__tag-line {
  margin: 0;
  padding: 0;
  margin-bottom: 25px;
  color: #ffffff;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
}
.promo__petfood {
  text-align: center;
}
@media (min-width: 768px) {
  .promo {
    margin-bottom: 25px;
    background-color: #ffffff;
  }
  .promo__title {
    margin-bottom: 45px;
    color: #111111;
    font-size: 60px;
    line-height: 60px;
  }
  .promo__tag-line {
    margin-bottom: 45px;
    color: #111111;
    font-size: 20px;
    line-height: 20px;
    text-align: left;
  }
  .promo__wrapper {
    padding-top: 50px;
    padding-left: 90px;
    padding-right: 50px;
    display: flex;
    flex-direction: column;
  }
  .promo__petfood {
    text-align: left;
    order: 4;
  }
  .promo__petfood-pic {
    width: 100%;
    height: 580px;
    margin-top: -80px;
  }
  .promo__programm-choice {
    padding: 15px 22px;
    width: auto;
    order: 3;
    align-self: flex-start;
    font-size: 20px;
    line-height: 20px;
    z-index: 2;
  }
}
@media (min-width: 1300px) {
  .promo {
    margin-bottom: 80px;
  }
  .promo::before {
    position: absolute;
    content: "";
    top: -136px;
    left: 50%;
    height: 585px;
    width: 50%;
    background-color: #65a93c;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: right;
    z-index: 2;
  }
  .promo__wrapper {
    padding-top: 25px;
    padding-left: 80px;
  }
  .promo__title {
    max-width: 420px;
  }
  .promo__tag-line {
    margin-bottom: 55px;
  }
  .promo__petfood-pic {
    width: 400px;
    height: 445px;
    margin-top: -346px;
    margin-right: 277px;
  }
  .promo__petfood {
    text-align: right;
    z-index: 4;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .promo::before {
    background-image: url("../img/index-background-desktop@2x.jpg");
    background-size: 1440px auto;
  }
}
.dietary-plan {
  margin-bottom: 30px;
  position: relative;
  box-sizing: border-box;
}
.dietary-plan__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.dietary-plan__item {
  position: relative;
  background-color: #f2f2f2;
  margin-bottom: 20px;
  padding: 40px 20px 35px;
}
.dietary-plan__item::before {
  position: absolute;
  content: "";
  top: 34px;
  left: 19px;
  background-color: #68b738;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.dietary-plan__item--slim::after {
  position: absolute;
  content: "";
  top: 34px;
  left: 28px;
  width: 34px;
  height: 50px;
  background-image: url("../img/icon-cat-slim.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.dietary-plan__item--pro::after {
  position: absolute;
  content: "";
  top: 47px;
  left: 9px;
  width: 70px;
  height: 30px;
  background-image: url("../img/icon-cat-pro.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.dietary-plan__title {
  margin: 0;
  margin-left: 63px;
