    top: 100px;
    left: 430px;
    width: 200px;
    height: 200px;
  }
  .dietary-plan__item--slim::after {
    top: 88px;
    left: 455px;
    width: 142px;
    height: 196px;
  }
  .dietary-plan__item--pro::after {
